.block-type--banner_announcement.block-view-mode--default {
  padding-right: 3em;
  padding-left: 3em;
  border: 2px solid #7ba1af;
}
.block-type--banner_announcement.block-view-mode--default .block-wrap {
  grid-template-rows: 5em auto;
